@import "../../css/shared.scss";
.matrix-detail {
    padding-top: px(70);
    .info {
        .info-name,
        .info-num {
            display: flex;
            justify-content: space-between;
            align-items: center;
            span {
                display: inline-block;
                font-weight: 500;
            }
        }
        .info-name {
            span {
                color: #ffffff;
                font-size: px(36);
                font-weight: 500;
            }
        }
        .info-num {
            margin-top: px(26);
            span {
                &:nth-child(1) {
                    color: #bb74e6;
                    font-size: px(48);
                }
                &:nth-child(2) {
                    display: flex;
                    align-items: center;
                    img {
                        display: inline-block;
                        width: px(36);
                        // height: px(26);
                        outline: 0;
                        border: 0;
                        margin-right: px(10);
                    }
                    span {
                        font-size: px(30);
                    }
                }
            }
        }
    }
    .matrix {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: px(15);
        .parent-id {
            display: flex;
            align-items: center;
            width: px(300);
            height: px(62);
            padding: px(10) px(12);
            border: 1px solid #bb73e7;
            border-radius: px(5);
            box-sizing: border-box;
            img {
                display: inline-block;
                width: px(33);
                transform: rotate(-90deg);
            }
            span {
                color: #bb73e7;
                font-size: px(47);
                margin-left: px(12);
            }
        }
    }
    .matrix-c {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin: px(30) 0;
        .left,
        .right {
            display: flex;
            flex-grow: 0;
            align-items: center;
            a {
                display: inline-block;
                width: px(33);
                height: px(33);
                outline: 0;
                border-radius: 50%;
                background-color: #17c1e5;
            }
            span {
                display: inline-block;
                padding: px(9) px(13);
                color: #ffffff;
                font-size: px(28);
                border-radius: px(4);
                background-color: #057d95;
            }
        }
        .left a {
            margin-right: px(20);
            background: #17c1e5 url("/images/arrow_left.png") no-repeat center center;
            background-size: 60%;
        }
        .right a {
            margin-left: px(20);
            background: #17c1e5 url("/images/arrow_left.png") no-repeat center center;
            background-size: 60%;
            transform: rotate(180deg);
        }
        .main {
            flex-shrink: 0;
            width: px(190);
            max-width: px(290);
            height: px(100);
            background-color: #17c1e5;
            border-radius: px(10);
        }
    }
    .tips {
        margin: 0 px(30);
        p {
            position: relative;
            padding-left: px(41);
            color: #ffffff;
            margin: px(30) 0 0 0;
            &::before {
                display: block;
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: px(22);
                height: px(22);
                border-radius: 50%;
            }
            &:nth-child(1) {
                &::before {
                    background-color: #03d1fd;
                }
            }
            &:nth-child(2) {
                &::before {
                    background-color: #fd87a9;
                }
            }
        }
    }
    .divider {
        width: px(690);
        // height: px(2);
        margin: px(45) auto;
        border-bottom: px(2) solid;
        border-image: $linear-gradient 1;
        // background: linear-gradient( 270deg, rgba(23, 193, 229, 1), rgba(187, 115, 231, 1));
    }
    .record {
        width: px(690);
        margin: 0 auto;
        .fixed-header {
            span {
                display: inline-block;
                font-size: px(28);
                color: #ffffff;
            }
        }
        .list {
            max-height: px(440);
            padding-bottom: px(48);
            overflow-x: hidden;
            overflow-y: auto;
            &::-webkit-scrollbar {
                display: none;
            }
            ul {
                margin: 0;
                padding: 0;
                li {
                    margin-top: px(35);
                    list-style-type: none;
                    span {
                        display: inline-block;
                        color: #ffffff;
                        font-size: px(24);
                    }
                }
            }
        }
        .id {
            width: 16%;
            padding-right: 2%;
        }
        .date {
            width: 30%;
        }
        .dos-usd {
            width: 18%;
            padding-left: 2%;
        }
        .wallet {
            width: 30%;
            text-align: right;
            a {
                display: inline-block;
                width: px(26);
                height: px(28);
                background: url("/images/icon_copy.png") no-repeat center;
                background-size: 100%;
                margin-left: px(15);
                color: transparent;
            }
        }
    }
}